*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.box{
    display: flex;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    background-origin: border-box;
    padding: 3rem 3rem;
    opacity: 0.5;
    background-color: rgba(0, 0, 0, 0.2);
    margin-bottom: 3rem;
}

.text h1{
    color: yellow;
    border-left: 8px solid yellow;
    padding-left: 8px;
    margin-bottom: 30px;
    opacity: 1;
}
.flex-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.Single-contain{
   height: 450px;
   width: 350px;
   opacity: 1;
   margin-right: 1rem;
}

.Single-contain img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 0 2px 4px rgba(241, 229, 229, 0.96);
    transition: transform 0.3s ease;
}

.Single-contain img:hover {
    transform: scale(1.05);
} 


.lists{
width: 40%;
border: 1px solid black;
padding: 15px;
border-radius: 10px;  
opacity: 1;
background-color: rgba(50, 49, 45, 0.839);
/* color: white; */
}
.lists h2{
    color: yellow;

}
/* .lists .para{
    margin-bottom: 2rem;
    color: #fff;
} */

.lists .para span{
    padding: 10px 20px;
    border-radius: 15px;
    background-color: yellow;
    color: black;
    margin:5px;
    font-weight: 600;
    
}

.lists button{
    padding: 10px 20px;
    border-radius: 10px;
    background-color: yellow;
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: 600;
    /* margin-top: 1rem !important; */
}
    


.tailer-modal{
    position: relative;
}


.tailer-modal button{
    position: absolute;
    top: 5px;
    right: -150px;
    color: #000;
    background-color: #fff;
    
    padding: 10px;
    border-radius: 50%;
}
.tailer-modal button:hover{
    color: #fff;
    background-color: #4641416c;
}